.Cover {
    background-color: blue;
    position: relative;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    overflow: hidden;
    z-index: 1;
}

.video-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}